* {
	margin: 0px; 
	padding: 0px; 
	box-sizing: border-box;
}
html, body{
    width: 100%;
    height:100%;
}

.base {
    display: flex;
    flex-flow: column;
    height: 100vh;
}
.base .header {
  flex: 0 1 auto;
  text-align: center;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  background-color: #1b262c;
  color: rgb(223, 217, 217);
}